* {
  box-sizing: border-box;
}
.button-element{
  font-family: "Roboto";
  color:#db1c48;
  width: 80px;
  height: 30px;
  border-radius: 10px;
  border-color: #db1c48;
  margin: 15px;
  border-style: solid;
  border-width: 2px;
  background-color: #ffffff;
}
.ghani{
  font-family: "Roboto";
  color:#ffffff;
  width: 80px;
  height: 30px;
  border-radius: 10px;
  border-color: #db1c48;
